import { TransformToolComponent } from 'src/app/core/transform-tool/transform-tool.component';
import jsonToXml from 'jsontoxml';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CoreModule } from '../../core/core.module';
import * as readme from './readme.md';

export class JsonToXmlComponent extends TransformToolComponent {
    id = 'json-to-xml';
    action = 'Convert';
    schema = {
        type: 'object',
        properties: {
            input: { type: 'string', title: 'JSON', description: 'JSON to Convert' },
            advanced: {
                type: 'object',
                properties: {
                    xmlHeader: { type: 'boolean', description: 'Add XML header' },
                    prettyPrint: { type: 'boolean', default: true, description: 'Make XML human readable' }
                },
                "x-schema-form": {
                    type: "section",
                    expandable: true,
                    expande: false,
                    title: 'Advanced'
                }
            }
        },
        required: ['input']
    };

    onTransform(transform: any): string {
        return jsonToXml(transform.input, transform.advanced);
    }

    documentation = readme.default;
}

@NgModule({
    declarations: [
        JsonToXmlComponent,
    ],
    imports: [
        CoreModule,
        RouterModule.forChild([
            { path: '', component: JsonToXmlComponent }
        ])
    ]
  })
export class JsonToXmlModule { }